@import "../Core/variables.scss";


.color-box {
    display: flex;
    justify-content: space-between;
    margin: 1rem 0;

    &__title {
        margin-right: 7rem;
        margin-left: 2.3rem;
        font-size: 1.4rem;
        font-weight: normal;
        text-transform: uppercase;
    }

    &__palettes {
        margin-top: 0.6rem;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
    }
}

.palette {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;

    &-container {
        margin-bottom: 2rem;

        &__label {
            display: flex;
            justify-content: center;
            align-items: center;
        }

        &__input {
            min-width: 1.7rem;
            margin-right: 0.5rem;
        }
    }

    &__box--darkGreen {
        background-color: $darkGreen;
        width: 3rem;
        height: 2.5rem;
    }

    &__box--medGreen {
        background-color: $mediumBlue;
        width: 3rem;
        height: 2.5rem;
    }

    &__box--lightGreen {
        background-color: $lightGreen;
        width: 3rem;
        height: 2.5rem;
    }

    &__box--darkRed {
        background-color: $darkRed;
        width: 3rem;
        height: 2.5rem;
    }

    &__box--medRed {
        background-color: $red;
        width: 3rem;
        height: 2.5rem;
    }

    &__box--lightRed {
        background-color: $darkOrange;
        width: 3rem;
        height: 2.5rem;
    }

    &__box--darkGray {
        background-color: $bluishGray;
        width: 3rem;
        height: 2.5rem;
    }

    &__box--beige {
        background-color: $yellowishBeige;
        width: 3rem;
        height: 2.5rem;
    }

    &__box--lightGray {
        background-color: $lightBlue;
        width: 3rem;
        height: 2.5rem;
    }
}